import { ADD_TASK, COMPLETE_TASK, DELETE_TASK, SET_EDIT_TASK, GET_TASKS, EDIT_TASK, ADD_TAG, LOAD_TAGS, SET_TAG } from '../actions/types'

const initialState = {
  loading: true,
  tags: ['All', 'Not Complete', 'Complete'],
  tasks: [], //id,task,tags,status
  editTask: null,
  currentTag: 'All',
}

export default function taskReducer(state = initialState, action) {
  switch (action.type) {
    case ADD_TASK:
      return {
        ...state, tasks: [action.payload, ...state.tasks], loading: false
      }

    case ADD_TAG:
      return {
        ...state,
        loading: false,
        tags: [...new Set(state.tags.concat(action.payload))]
      }

    case GET_TASKS:
      return {
        ...state, tasks: action.payload, loading: false
      }

    case LOAD_TAGS:
      return {
        ...state, tags: action.payload
      }

    case COMPLETE_TASK:
      return {
        ...state,
        loading: false,
        tasks: state.tasks.map(task => task.id === action.payload ? {
          ...task, isComplete: !task.isComplete
        } : task)
      }

    case SET_EDIT_TASK:
      return {
        ...state,
        loading: false,
        editTask: state.tasks.filter(task => task.id === action.payload ? task : null)[0]
      }

    case EDIT_TASK:
      return {
        ...state,
        loading: false,
        tasks: state.tasks.map(task => task.id === action.payload.id ? {
          ...task, text: action.payload.text, tags: action.payload.tags
        } : task)
      }

    case DELETE_TASK:
      return {
        ...state,
        tasks: state.tasks.filter(task => task.id !== action.payload),
        loading: false
      }

    case SET_TAG:
      return {
        ...state, currentTag: action.payload
      }
    default:
      return state
  }
}


